import React from 'react'
import { useState } from 'react'
import { useContext } from 'react'
import { CartContext } from '../../CartContext';
import { useTranslation } from 'react-i18next'

const Overlay = ({ product }) => {
    const [cart, setCart] = useContext(CartContext)
    const [amount, setAmount] = useState(0)
    const removeOverlay = (e) =>{
        e.target.parentElement.parentElement.style.display = 'none';
    }

    const addToCart = (product, e)=>{
        setCart([...cart, {product:product}]);
        removeOverlay(e);
    }

    const { t } = useTranslation();
  return (
    <div className='overlay'>
        <div onClick={removeOverlay} className="shadow"></div>
        <div className="overlayContainer">
            <div className="heading">
                <h2 className='headingOne'>{product.fields.name}</h2>
                <h2  className='headingOne'>Available forms</h2>
            </div>
            <div className="size">
                <div className="name">500g</div>
                <div className="name">${product.fields.price}</div>
                <input type="number" value={amount} onChange={(e)=>setAmount(e.target.value)} />
            </div>
            <div className="size">
                <div className="name">1kg</div>
                <div className="name">${product.fields.price}</div>
                <input type="number" value={amount} onChange={(e)=>setAmount(e.target.value)} />
            </div>
            <p onClick={(e)=>{addToCart(product,e)}}  className="controlLinks callToAction"> $200 { t("other_products.buy") }</p>
        </div>
    </div>
  )
}

export default Overlay